import { BASE_IMAGE_URL,bindUser,getUserInfo } from "@/utils/api";

export default {
	data() {
		return {
			bind_bg: BASE_IMAGE_URL+"bind_bg.png",
			name_bg: BASE_IMAGE_URL+"name_bg.png",
			mobile_bg: BASE_IMAGE_URL+"mobile_bg.png",
			verify_bg: BASE_IMAGE_URL+"verify_bg.png",
			email_bg: BASE_IMAGE_URL+"email_bg.png",
			card_bg: BASE_IMAGE_URL+"card_bg.png",
			info_bg: BASE_IMAGE_URL+"info_bg.png",
			register_bg: BASE_IMAGE_URL+"register_bg.png",
			m_truename: '',
			m_email: '',
			m_id_number: '',
			m_phone: '',
			phone_code: '',
			codeBtn: {
				text: 'SMS CODE',
				waitingCode: false,
				count: this.seconds
			},
			seconds: 60,
		}
	},
	methods: {
		async getCodeAction(){
			if((!this.m_phone) || (this.m_phone.length != 11)){
				uni.showToast({
				  icon: 'none',
				  title: '手机号输入错误',
				  duration: 1000,
				  position:'bottom'
				});
				return;
			};
			
			// let res = await sendCode(this.phone);
			let res = {status: 0};
			if (res.code == 1000) {
				uni.showToast({
					icon: 'none',
					title: '获取验证码成功',
					duration: 1000,
					position:'bottom'
				});
				this.countDown();
			} else {
				uni.showToast({
					icon: 'none',
					title: res.message,
					duration: 1000,
					position:'bottom'
				});
			}
			
		},
		countDown: function() {
			this.codeBtn.count = this.seconds;
			this.codeBtn.text = this.codeBtn.count + 's';
		
			let countdown = setInterval(() => {
				this.codeBtn.count--;
				this.codeBtn.text = this.codeBtn.count + 's';
				if (this.codeBtn.count < 0) {
					clearInterval(countdown);
					this.codeBtn.text = '重新发送';
				}
			}, 1000);
		},
		async bindUser(){
			if((!this.m_phone) || (this.m_phone.length != 11)){
				uni.showToast({
				  icon: 'none',
				  title: '手机号输入错误',
				  duration: 1000,
				  position:'bottom'
				});
				return;
			};
			if(!this.m_truename){
				uni.showToast({
					icon: 'none',
					title: '请输入姓名',
					duration: 1000,
					position:'bottom'
				});
				return;
			}
			
			if(!this.m_email){
				uni.showToast({
					icon: 'none',
					title: '请输入邮箱',
					duration: 1000,
					position:'bottom'
				});
				return;
			}
			
			if(!this.m_id_number){
				uni.showToast({
					icon: 'none',
					title: '请输入身份证号',
					duration: 1000,
					position:'bottom'
				});
				return;
			}
			
			if(!this.phone_code){
				uni.showToast({
					icon: 'none',
					title: '请输入验证码',
					duration: 1000,
					position:'bottom'
				});
				return;
			}
			
			
			let params = {
				m_openid: uni.getStorageSync("openid"),
				m_truename: this.m_truename,
				m_email: this.m_email,
				m_id_number: this.m_id_number,
				m_phone: this.m_phone,
				m_code: this.m_code,
				type: 1,
				phone_code: this.phone_code
			};
			let res = await bindUser(params);
			if(res.code == 1000){
				uni.showToast({
					icon: 'none',
					title: '注册成功',
					duration: 1000,
					position:'bottom'
				});
				
				setTimeout(() => {
					this.getUserInfo();
				}, 1000);
			}
		},
		
		async getUserInfo(){
			let res = await getUserInfo(uni.getStorageSync('openid'));
			console.log(res);
			if(res.code == 1000){
				wx.setStorageSync('userInfo',res.data);
				uni.redirectTo({
					url:'/pages/main/main'
				})
			}else{
				uni.showToast({
					icon: 'none',
					title: res.msg,
					duration: 1000
				});
			}
		}
		
	}
}
